import React, { Fragment } from 'react';

import { Popover, Nav, NavLink, Col, Row, NavItem, Button } from 'reactstrap';

import bg4 from 'assets/utils/images/dropdown-header/city5.jpg';

import { faAngleUp } from '@fortawesome/free-solid-svg-icons';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

class FooterMegaMenu extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.toggle2 = this.toggle2.bind(this);
    this.state = {
      popoverOpen: false,
      popoverOpen2: false
    };
  }

  toggle() {
    this.setState({
      popoverOpen: !this.state.popoverOpen
    });
  }

  toggle2() {
    this.setState({
      popoverOpen2: !this.state.popoverOpen2
    });
  }

  state = {};

  render() {
    return (
      <Fragment>
        <Nav className="header-megamenu">
          <NavItem>
            <NavLink
              href="javascript:void(0);"
              id="PopoverMegaMenuFooter"
              onClick={this.toggle}
            >
              Footer Menu
              <FontAwesomeIcon
                className="ml-2 opacity-8"
                icon={faAngleUp}
              />
            </NavLink>
          </NavItem>
          <Popover
            className="rm-max-width"
            fade={false}
            hideArrow
            isOpen={this.state.popoverOpen}
            placement="top-start"
            target="PopoverMegaMenuFooter"
            toggle={this.toggle}
            trigger="legacy"
          >
            <div className="dropdown-mega-menu dropdown-mega-menu-sm">
              <div className="grid-menu grid-menu-2col">
                <Row className="no-gutters">
                  <Col
                    sm="6"
                    xl="6"
                  >
                    <Nav vertical>
                      <NavItem className="nav-item-header">Overview</NavItem>
                      <NavItem>
                        <NavLink href="javascript:void(0);">
                          <i className="nav-link-icon lnr-inbox" />
                          <span>Contacts</span>
                        </NavLink>
                      </NavItem>
                      <NavItem>
                        <NavLink href="javascript:void(0);">
                          <i className="nav-link-icon lnr-book" />
                          <span>Incidents</span>
                          <div className="ml-auto badge badge-pill badge-danger">
                            5
                          </div>
                        </NavLink>
                      </NavItem>
                      <NavItem>
                        <NavLink href="javascript:void(0);">
                          <i className="nav-link-icon lnr-picture" />
                          <span>Companies</span>
                        </NavLink>
                      </NavItem>
                      <NavItem>
                        <NavLink
                          disabled
                          href="javascript:void(0);"
                        >
                          <i className="nav-link-icon lnr-file-empty" />
                          <span>Dashboards</span>
                        </NavLink>
                      </NavItem>
                    </Nav>
                  </Col>
                  <Col
                    sm="6"
                    xl="6"
                  >
                    <Nav vertical>
                      <NavItem className="nav-item-header">
                        Sales &amp; Marketing
                      </NavItem>
                      <NavItem>
                        <NavLink href="javascript:void(0);">Queues</NavLink>
                      </NavItem>
                      <NavItem>
                        <NavLink href="javascript:void(0);">
                          Resource Groups
                        </NavLink>
                      </NavItem>
                      <NavItem>
                        <NavLink href="javascript:void(0);">
                          Goal Metrics
                          <div className="ml-auto badge badge-warning">3</div>
                        </NavLink>
                      </NavItem>
                      <NavItem>
                        <NavLink href="javascript:void(0);">Campaigns</NavLink>
                      </NavItem>
                    </Nav>
                  </Col>
                </Row>
              </div>
            </div>
          </Popover>
          <NavItem>
            <NavLink
              href="javascript:void(0);"
              id="PopoverGridFooter"
              onClick={this.toggle2}
            >
              Grid Menu
              <div className="badge badge-dark ml-0 ml-1">
                <small>NEW</small>
              </div>
              <FontAwesomeIcon
                className="ml-2 opacity-8"
                icon={faAngleUp}
              />
            </NavLink>
          </NavItem>
          <Popover
            className="rm-max-width popover-custom"
            fade={false}
            hideArrow
            isOpen={this.state.popoverOpen2}
            placement="top-start"
            target="PopoverGridFooter"
            toggle={this.toggle2}
            trigger="legacy"
          >
            <div className="dropdown-menu-header">
              <div className="dropdown-menu-header-inner bg-tempting-azure">
                <div
                  className="menu-header-image opacity-1"
                  style={{
                    backgroundImage: 'url(' + bg4 + ')'
                  }}
                />
                <div className="menu-header-content text-dark">
                  <h5 className="menu-header-title">Two Column Grid</h5>
                  <h6 className="menu-header-subtitle">
                    Easy grid navigation inside popovers
                  </h6>
                </div>
              </div>
            </div>
            <div className="grid-menu grid-menu-2col">
              <Row className="no-gutters">
                <Col sm="6">
                  <Button
                    className="btn-icon-vertical btn-transition-text btn-transition btn-transition-alt pt-2 pb-2"
                    color="dark"
                    outline
                  >
                    <i className="lnr-lighter text-dark opacity-7 btn-icon-wrapper mb-2">
                      {' '}
                    </i>
                    Automation
                  </Button>
                </Col>
                <Col sm="6">
                  <Button
                    className="btn-icon-vertical btn-transition-text btn-transition btn-transition-alt pt-2 pb-2"
                    color="danger"
                    outline
                  >
                    <i className="lnr-construction text-danger opacity-7 btn-icon-wrapper mb-2">
                      {' '}
                    </i>
                    Reports
                  </Button>
                </Col>
                <Col sm="6">
                  <Button
                    className="btn-icon-vertical btn-transition-text btn-transition btn-transition-alt pt-2 pb-2"
                    color="success"
                    outline
                  >
                    <i className="lnr-bus text-success opacity-7 btn-icon-wrapper mb-2">
                      {' '}
                    </i>
                    Activity
                  </Button>
                </Col>
                <Col sm="6">
                  <Button
                    className="btn-icon-vertical btn-transition-text btn-transition btn-transition-alt pt-2 pb-2"
                    color="focus"
                    outline
                  >
                    <i className="lnr-gift text-focus opacity-7 btn-icon-wrapper mb-2">
                      {' '}
                    </i>
                    Settings
                  </Button>
                </Col>
              </Row>
            </div>
            <Nav vertical>
              <NavItem className="nav-item-divider" />
              <NavItem className="nav-item-btn clearfix">
                <div className="float-left">
                  <Button
                    color="link"
                    size="sm"
                  >
                    Link Button
                  </Button>
                </div>
                <div className="float-right">
                  <Button
                    className="btn-shadow"
                    color="info"
                    size="sm"
                  >
                    Info Button
                  </Button>
                </div>
              </NavItem>
            </Nav>
          </Popover>
        </Nav>
      </Fragment>
    );
  }
}

export default FooterMegaMenu;
